.about {
  height: 100vh;
  background: #d7d7d7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: darkgray;
  h1 {
    font-size: 3em;
    margin: 0;
  }
  p {
    font-size: xx-large;
    margin: 0;
  }
  @media (max-width: 450px) {
    img {
      width: 20px;
      height: 20px;
    }
  }
}